import styled from 'styled-components';

// Create a tile component that'll render a <h2> tag with some styles
export default styled.h2`
  font-family: "Merriweather Sans";
  font-size: calc(1vw + 1vh);
  font-weight: 400;
  letter-spacing: -1px;
  color: white;
`;



